import './index.css';
import React from 'react';

function App() {
  const[count, setCount] = React.useState(0);

  const Plus = () =>{
    setCount(count + 1);
  }
  const Minus = () =>{
    setCount(count - 1);
  }
  const Sqrt = () =>{
    setCount(count * 2);
  }
  return (
    <div className="App">
      <div>
        <h2>Счетчик:</h2>
        <h1>{count}</h1>
        <button onClick={Minus} className="minus">Минусуй</button>
        <button onClick={Plus} className="plus">Плюсуй</button>
        <button onClick={Sqrt} className="sqrt">Умножай на два, дружище))</button>
      </div>
    </div>
  );
}

export default App;
